import React from 'react'
import {Link} from 'gatsby'
import {css} from '@emotion/core'
import useAllPosts from './../hooks/use-allposts';
import Layout from './../components/layout';
import Searchbar from '../components/searchbar'

const HomePage = ()=>{
    let allNodes = useAllPosts()
    console.log("before allNodes ",allNodes)
    
    allNodes = allNodes.sort(function(a, b) {
        return a.frontmatter.articleid > b.frontmatter.articleid ? -1 : 1
    })
    console.log("after allNodes ",allNodes)

    return(
        <Layout>
            <div>
                <Searchbar allNodes={allNodes}/>
            </div>
        </Layout>
    )
}

export default HomePage